import * as React from 'react';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Avatar from '@mui/material/Avatar';
import InsightsIcon from '@mui/icons-material/Insights';
import NightlifeIcon from '@mui/icons-material/Nightlife';
import VideoCameraFrontIcon from '@mui/icons-material/VideoCameraFront';
import SearchIcon from '@mui/icons-material/Search';

export default function IconList() {
  const list = [
    {
        icon: <VideoCameraFrontIcon/>,
        title: "高级算法工程师",
        sub: "快手（2021.7 ~ 现在）",
        body: "AI算法、推荐算法、运筹规划、及大数据分析工作"
    },{
        icon: <InsightsIcon/>,
        title: "移动部总监、首席科学家",
        sub: "中国搜索（2014.1 ~ 2021.7）",
        body: "中国搜索移动端业务，新华社多模态国家重点实验室相关项目"
    },{
        icon: <NightlifeIcon/>,
        title: "高级研发工程师",
        sub: "网易（2012.9～2013.12）",
        body: "网易新闻客户端的相关应用开发工作"
    },{
        icon: <SearchIcon/>,
        title: "前端开发工程师",
        sub: "百度 （2011.6～2012.8）",
        body: "百度用户中心、贴吧等前端开发工作"
    },
  ]

  return (
    <List sx={{ width: '100%', bgcolor: 'background.paper' }}>
        {list.map((e, idx) => {
            return <ListItem key={idx}>
            <ListItemAvatar>
              <Avatar>{e.icon}</Avatar>
            </ListItemAvatar>
            <ListItemText primary={e.title} secondary={e.sub} style={{width: '100px'}} />
            <p>{e.body}</p>
          </ListItem>
        })} 
    </List>
  );
}